.barContainer {
	background-color: rgb(250, 250, 250);
    padding: 10px 20px;
    position: fixed;
    z-index: 10;
    height: 48px;
    width: 100%;
    
}

@mixin  taggleTag {
    display: inline-block;
    position: relative;
    vertical-align: middle;
	width: 20px;
	height: 20px;
	margin-right: 22px;
    background: {
        size: 20px 20px;
        repeat: no-repeat;
        position: center;
    };
}

.taggleListOn{
    @include taggleTag();
    background-image: url('./list_blue.svg');
}
.taggleListOff{
    @include taggleTag();
    background-image: url('./list_gray.svg');
}
.taggleThumbOn{
    @include taggleTag();
    background-image: url('./thumb_blue.svg');
}
.taggleThumbOff{
    @include taggleTag();
    background-image: url('./thumb_gray.svg');
}