.resultSectionList,
.resultSectionThumb {
	padding: 20px;
	position: relative;
	overflow-y: auto;
	margin-top: 48px;
}

.resultUL {
	display: flex;
	flex-flow: row wrap;
	align-content: flex-start;
	justify-content: space-between;

	> li {
		display: flex;
		vertical-align: top;
		font-size: 14px;
		margin-bottom: 14px;
		transition-property: all;
		transition-duration: 0.3s;

		> div {
			display: flex;
			vertical-align: top;
			line-height: 20px;
			width: 100%;
			margin-right: 20px;
			background-color: #fff;
			box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
		}
	}
}

.resultSectionList {
	li {
		width: 100%;

		> div {
			padding: 10px;
			> .compLogo {
				width: 32px;
				height: 32px;
				margin-right: 20px;
			}
		}
	}
}

.resultSectionThumb {
	li {
		width: 50%;

		> div {
			flex-direction: column;
			padding: 10px 20px;
			text-align: center;

			> .compLogo {
				display: block;
				width: 64px;
				height: 64px;
				margin: 0 auto 1em;
			}
		}
	}
}
