.pod {
    width: 50%;

    &:nth-of-type(odd) {
        >.dice{
            margin-right: 7px;
        }
		
	}

	&:nth-of-type(even) {
        >.dice{
            margin-left: 7px;
        }
		
    }
}
.dice {
	background: {
        color: #fff;
    }
    

	box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.1);
	
    
    >img{
        width: 100%;
    }
}
