$red: #e01c34;
$white: #ffffff;
$light: #f1f1f1;
$gray: #888;

* {
	box-sizing: border-box;
	text-transform: none !important;
}
html,
body {
	margin: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 20px;
	height: 100%;
	width: 100%;
}
ul {
	padding-left: 0;
}
p {
	margin: 12px 0;
}
hr {
	border: 0;
	height: 1px;
	background-color: #ccc;
}
.text-gray {
	color: $gray;
}
.text-white {
	color: $white;
}
.text-red {
	color: $red;
}

.fl-left {
	float: left;
}
.fl-right {
	float: right;
}
.font-14 {
	font-size: 14px;
}
.font-16 {
	font-size: 16px;
}
.font-24 {
	font-size: 24px;
}
.pl-10 {
	padding-left: 10px;
}
.pr-10 {
	padding-right: 10px;
}
.px-10 {
	padding-left: 10px;
	padding-right: 10px;
}
.p-10{
	padding: 10px;
}
.p-20{
	padding: 20px;
}
.mt-10 {
	margin-top: 10px;
}
.mb-10 {
	margin-bottom: 10px;
}
.mr-10 {
	margin-right: 10px;
}
.ml-10 {
	margin-left: 10px;
}
.mx-10 {
	margin-left: 10px;
	margin-right: 10px;
}
.my-10 {
	margin-top: 10px;
	margin-bottom: 10px;
}
.m-10 {
	margin: 10px;
}
.mx-auto {
	margin-left: auto;
	margin-right: auto;
}
.text-center {
	text-align: center;
}

