.overlayClear {
	position: absolute;
	width: 16px;
	height: 16px;
	line-height: 16px;
	text-align: center;
	top: 1px;
	right: 7px;
	background-color: #bbb;
	border-radius: 8px;
	font-size: 18px;
	font-family: 'Times New Roman', Times, serif;
	font-weight: bold;
	color: white;
	cursor: pointer;
}
